import React from "react";
import { useRef, useState } from "react";
import "./Contact.css";
import emailjs from "@emailjs/browser";


function Contact() {
  const formRef = useRef();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");

  const SubmitHandler = (e) => {
    e.preventDefault();
    if (name == "" || email == ""  || message == "") {
      alert("Sir/Mam Please Fill all the details");
    }
    else 
    {
      alert("Thank You for your Message! I'll reply ASAP");
      setEmail("")
      setMessage("")
      setName("")
    }
    
   

    emailjs
      .sendForm(
        "service_r1vkgjd",
        "template_2kh73yy",
        formRef.current,
        "MIoilCmikAH0We0_G"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <section id="contact">
      <h2 id="contact-header">Feel free to Leave a Message </h2>

      <div className="c">
        <div className="c-bg">
          <div className="c-wrapper">
            <div className="c-left">
              <h1 className="c-title">Let's Connect</h1>
              <div className="c-info">
                <div className="c-info-item">
                  <img
                    className="c-icon"
                    src="https://i.pinimg.com/originals/84/4e/8c/844e8cd4ab26c82286238471f0e5a901.png"
                    alt=""
                  />
                  <h6>+91 7005763599</h6>
                </div>
                <div className="c-info-item">
                  <img
                    className="c-icon"
                    src="data:image/png;base64,"
                    alt=""
                  />
                 <h6> mail4sandeep44@gmail.com</h6>
                </div>
                <div className="c-info-item">
                  <img
                    className="c-icon"
                    src="https://www.citypng.com/public/uploads/preview/address-location-map-black-icon-symbol-download-png-11640206707qbbrqva5rt.png"
                    alt=""
                  />
                  <h6>Lunglei, Mizoram, India</h6>
                </div>
              </div>
              <div className="colz">
                <div className="colz-icon">
                  <a
                    href="https://github.com/sandeepprogrammer64"
                    target="blank"
                    title="Github"
                  >
                    <img
                      className="icons"
                      src="https://cdn-icons-png.flaticon.com/512/25/25231.png"
                      alt="Sandeep's GitHub"
                      rel="noreferrer"
                    />
                  </a>

                  <a
                    href="https://www.linkedin.com/in/sandeep-prajapati64/"
                    target="blank"
                    title="Linkedin"
                  >
                    <img
                      className="icons"
                      src="https://cdn-icons-png.flaticon.com/512/145/145807.png"
                      alt=""
                    />
                  </a>
                  <a
                    href="https://twitter.com/Sandeep63259242"
                    target="blank"
                    title="Twitter"
                    
                  >
                    <img
                      src="data:image/png;base64,"
                      alt=""
                      className="icons"
                      
                    />
                  </a>
                  <a
                    href="https://www.instagram.com/smart_swag_feb/"
                    target="blank"
                    title="Instagram"
                  >
                    <img
                      src="data:image/jpeg;base64,"
                      alt=""
                      className="icons"
                    />
                  </a>
                </div>
              </div>
            </div>
            <div className="c-right">
              {/* <p >Your Message or Feedback will be appreciated</p> */}
              <form ref={formRef} onSubmit={SubmitHandler}>
                <input
                  type="text"
                  placeholder="Your Name"
                  name="user_name"
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
                <input
                  type="text"
                  name="user_email"
                  placeholder="Your Email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
                <textarea
                  rows="5"
                  placeholder="Your Message"
                  name="message"
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                ></textarea>
                <button>Send Message</button>
              </form>
            </div>
          </div>
        </div>
      </div>
      <div>
        {/* <h3 style={{ textAlign: "center", fontWeight: "bold" }}>
          Made By Sandeep Prajapati
        </h3> */}
      </div>
    </section>
  );
}

export default Contact;
